interface Props {
    time: number;
}
const getSeconds = (time:number) =>  (
    (time % 60) < 10 
        ? "0"+(time % 60).toFixed() 
        : (time % 60).toFixed()
)

const getMinutes = (time: number) => (
    Math.floor(time / 60) < 10 
        ? "0"+(time % 60).toFixed() 
        : (time % 60).toFixed()
)

const Timer = (props: Props) => <p><strong>Time: </strong>{getMinutes(props.time)}:{getSeconds(props.time)}</p>
export default Timer